<template>
    <div class="container">
        <section class="row">
            <div class="col-xs-12">
                <v-breadnav>惠群问答</v-breadnav>
            </div>
        </section>
        <section class="row question-list">
            <div class="col-xs-12 clearfix"> 
                <div class="title"><h3>惠群问答</h3></div>
                <ul class="nav-list col-xs-2">
                    <li class="active"><a href="#buy-explain" aria-controls="buy-explain" @click="getListData('080001')" role="tab" data-toggle="tab">购车说明</a></li>
                    <li><a href="#cost-explain" aria-controls="cost-explain" @click="getListData('080002')" role="tab" data-toggle="tab">费用说明</a></li>
                    <li><a href="#rent-plan" aria-controls="rent-plan" @click="getListData('080003')" role="tab" data-toggle="tab">租后方案</a></li>
                    <li><a href="#Transport-rules" aria-controls="Transport-rules" @click="getListData('080004')" role="tab" data-toggle="tab">用车规则</a></li>
                    <li><a href="#Insurance-services" aria-controls="Insurance-services" @click="getListData('080005')" role="tab" data-toggle="tab">保险服务</a></li>
                    <li><a href="#other-questions" aria-controls="other-questions" @click="getListData('080006')" role="tab" data-toggle="tab">其他问题</a></li>    
                </ul>               
                <div class="tab-content col-xs-10">
                     <div role="tabpanel" class="tab-pane active" id="buy-explain">
                         <ul class="questions-list" v-if="questionAnswerList.length != 0">
                            <li class='question' v-for="(item,index) in questionAnswerList" :key="index" :data-id = 'item.id'>
                                <h4>{{item.question}}</h4>
                                <p>{{item.answer}}</p>
                            </li>
                        </ul>
                     </div>
                    <div role="tabpanel" class="tab-pane" id="cost-explain">
                        <ul class="questions-list" v-if="questionAnswerList.length != 0">
                            <li class='question' v-for="(item,index) in questionAnswerList" :key="index" :data-id = 'item.id'>
                                <h4>{{item.question}}</h4>
                                <p>{{item.answer}}</p>
                            </li>
                        </ul>
                    </div>
                    <div role="tabpanel" class="tab-pane" id="rent-plan">
                        <ul class="questions-list" v-if="questionAnswerList.length != 0">
                            <li class='question' v-for="(item,index) in questionAnswerList" :key="index" :data-id = 'item.id'>
                                <h4>{{item.question}}</h4>
                                <p>{{item.answer}}</p>
                            </li>
                        </ul>
                    </div>
                    <div role="tabpanel" class="tab-pane" id="Transport-rules">
                        <ul class="questions-list" v-if="questionAnswerList.length != 0">
                            <li class='question' v-for="(item,index) in questionAnswerList" :key="index" :data-id = 'item.id'>
                                <h4>{{item.question}}</h4>
                                <p>{{item.answer}}</p>
                            </li>
                        </ul>
                    </div>
                    <div role="tabpanel" class="tab-pane" id="Insurance-services">
                        <ul class="questions-list" v-if="questionAnswerList.length != 0">
                            <li class='question' v-for="(item,index) in questionAnswerList" :key="index" :data-id = 'item.id'>
                                <h4>{{item.question}}</h4>
                                <p>{{item.answer}}</p>
                            </li>
                        </ul>
                    </div>
                    <div role="tabpanel" class="tab-pane" id="other-questions">
                        <ul class="questions-list" v-if="questionAnswerList.length != 0">
                            <li class='question' v-for="(item,index) in questionAnswerList" :key="index" :data-id = 'item.id'>
                                <h4>{{item.question}}</h4>
                                <p>{{item.answer}}</p>
                            </li>
                        </ul>
                    </div>
                </div>        
            </div>
        </section>
    </div>
    
</template>

<script>
import vBreadnav from "@/components/pc/breadNav"
import {PC_COMMONURL,SUCCESS_CODE} from '@/assets/js/common'
export default {
    data:function(){
        return{
            questionAnswerList:[]
        }
    },
    components:{
        vBreadnav
    },
    methods:{
        getListData:function(typeCd,pageNum){
            let _this = this;
            let data = {};
            if(pageNum){
                data.pageNum = pageNum;
            }else{
                data.pageNum = 1;
            }
            data.typeCd = typeCd;
            data.pageSize = 10;
            data.platForm = "020003";
             $.ajax({
                url:PC_COMMONURL+'/queryQuestionAnswerList',
                type:"get",
                data:data,
                dataType:'json',
                asnyc:true,
                success:function(data){
                    if(data.code == SUCCESS_CODE){
                        console.log(data.data);
                        _this.questionAnswerList = data.data.questionAnswerList;
                    }
                }
            })
        }
    },
    mounted:function(){
        this.getListData();
    }   
}
</script>



